<template>
  <div class="bill">
      <div class="bill-top">
          <div class="bill-top-logo">
             <van-notice-bar
                text="--------------v0.0.1---------------------"
                left-icon="volume-o"
                />
          </div>
          <div class="bill-top-details">
            <div class="bill-top-details-date" @click="show = true">
                {{selectYear}} <br><font>{{selectMonth}}</font>
            </div>
            <div class="bill-top-details-export">
                <br><font>0.00</font>
            </div>
            <div class="bill-top-details-import">
                <br><font>777.00</font>
            </div>
          </div>    
      </div>
      <div class="bill-detail">
          <div class="bill-detail-top">
          </div>
          <div class="bill-detail-body">
            <demo-section name="pull-refresh">
                <van-pull-refresh
                v-model="isLoading"
                @refresh="onRefresh"
                >
                <van-list
                        v-model="list.loading"
                        :finished="list.finished"
                        :finished-text="finishedText"
                        @load="onLoad()"
                    >
                    <van-cell
                        v-for="item in list.items"
                        :key="item">
                        <div class="list-Date">
                            <div class="list-Date-Detail">{{item.BillDate}}&nbsp&nbsp&nbsp&nbsp{{item.BillDay}}</div>
                            <div>:201.00&nbsp&nbsp&nbsp&nbsp:302</div>
                        </div>
                        <div class="list-Detail"  v-for="detailItem in item.Detail" :key="detailItem">
                            <img :src="detailItem.BillIcon">
                            <div class="list-Detail-Title">{{detailItem.BillTitle}}</div>
                            <div class="list-Detail-Money">{{detailItem.BillMoney}}</div>
                        </div>
                    </van-cell>
                    </van-list>
                </van-pull-refresh>
            </demo-section>
          </div>
           <div class="bill-detail-top">
          </div>
      </div>
      <div class="bill-Record" @click="showBill = true">
          <img src="../assets/add.png">
      </div >
      <div>
        <van-popup
            v-model="show"
            position="bottom"
            >
            <van-datetime-picker
                v-model="currentDate"
                type="year-month"
                @confirm="selYearMonth"
                @cancel="show = false"
            />
        </van-popup>
         <van-popup
            v-model="showBill"
            position="bottom"
            >
            <div class="selbill">
                <bill @closeBill="toCloseBill" @openDateTime="OverLayClick"></bill>
            </div>
        </van-popup>
        <div  v-if="OverLay" class="van-overlay" style="z-index: 2000;"></div>
    </div>
  </div>
</template>
<script>
import {List,Toast ,Tab, Tabs, PullRefresh,Cell,NoticeBar,Popup,DatetimePicker} from 'vant';
import bill from './bill.vue';
export default {
  name: 'alibabaPay',
  components: {
      [List.name]: List,
      [Toast.name]: Toast,
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [PullRefresh.name]: PullRefresh,
      [Cell.name]: Cell,
      [NoticeBar.name]: NoticeBar,
      [Popup.name]: Popup,
      [DatetimePicker.name]: DatetimePicker ,
      bill
  },
  data() {
    return {
      list: {
        items: [
            {
                BillDate: "0128",
                BillDay: "",
                Detail: [
                    {
                        BillIcon: require("../assets/food-active.png"),
                        BillTitle: "",
                        BillMoney: "-330.00"
                    },
                ]
            },{
                BillDate: "0127",
                BillDay: "",
                Detail: [
                    {
                        BillIcon: require("../assets/food-active.png"),
                        BillTitle: "",
                        BillMoney: "-182.00"
                    },{
                        BillIcon: require("../assets/food-active.png"),
                        BillTitle: "",
                        BillMoney: "-222.00"
                    }
                ]
            }
        ],
        refreshing: false,
        loading: false,
        error: false,
        finished: false
      },
      selectYear: "",
      selectMonth: "",
      count: 0,
      isLoading: false,
      basicUsage:"123",
      text: "",
      success: "",
      finishedText: "",
      show: false,
      showBill: false,
      currentDate: new Date(),
      OverLay: false
    };
  },
  watch(){
      
  },
  created(){
      this.init();
  },
  methods: {
    init() {
        this.selectYear = "2019"
        this.selectMonth = "01"
    },
    selYearMonth(){ 
        this.selectYear = this.currentDate.getFullYear() + ""
        let selectMonth = parseInt(this.currentDate.getMonth())  + 1
        selectMonth = selectMonth<10 ? "0"+selectMonth : selectMonth 
        this.selectMonth =  selectMonth + ""
        this.show = false;
    },
    onLoad() {
      const list = this.list;
      setTimeout(() => {
          list.finished = true;
    /*
        for (let i = 0; i < 10; i++) {
          const text = list.items.length + 1;
          list.items.push(text < 10 ? '0' + text : text);
        }
    */
        list.loading = false;
        // show error info in second demo
        if (index === 1 && list.items.length === 10 && !list.error) {
          list.error = true;
        } else {
          list.error = false;
        }
        list.finished = true;
        
      }, 500);
    },
    onRefresh(index) {
      const list = this.list[index];
      setTimeout(() => {
        list.items = [];
        list.error = false;
        list.finished = false;
        list.refreshing = false;
        window.scrollTo(0, 10);
      }, 1000);
    },
     onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.count++;
      }, 500);
    },
    toCloseBill(){
        this.showBill = false
    },
    OverLayClick(value){
        this.OverLay = value
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
