    color: rgb(30, 41, 61);
    height: 100%;
    overflow: hidden;
     background: #BEBECA;
}
.bill{
    display: flex;
    flex-direction: column; 
    height: 100%;
    &-top{
        height: 95px;
        background: #FF69B4;
        &-details{
            //background: black;
            display: flex;
            flex-direction: row;
            font{
                font-weight: bold;
                font-size: 23px;
            }
            &-date{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 20%;
                //background: brown;
            }
            &-import{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 40%;
                //background:cadetblue;
            }
            &-export{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 40%;
                //background:cornflowerblue;
            }
        }
    }
    &-detail{
    display: flex;
    flex-direction: row;
    flex-grow:1;
        &-top{
            height: 20px;
             flex-grow:1;
            background: #FF69B4;
        }
        &-body{

            flex-grow:18;
            background: white;
            border-top: yellow 2px solid;
        }
    }
    &-Record{
         height:30px;
         background:white;
         text-align: center;
         line-height: -10%;
         padding: 2px;
    }
    &-DateSelect{
        height:60px;
        background: 100px;
    }
}
  .van-pull-refresh {
      overflow:scroll;
      height: 100%;
  }
  .selbill{
      height: 442px;
      background: white;
  }
.list-Date{
    font-size: 13px;
    color: #BEBECA;
    display: flex;
    flex-direction: row;
    &-Detail{
        flex-grow: 1;
    }
}
.list-Detail{
    margin-top: 15px;
    margin-bottom: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
    &-Title{
        flex-grow: 1;
        padding-left: 10px;
    }
    &-Money{
       

    }
}
</style>
